.contact-section{
    // height: 90rem;
    // background-color: #e4e4e4;
    background-color: #ffffff;
    padding-bottom: 3rem;

    .contact{
        max-width: 130rem;
        margin: 0 auto;
        position: relative;
        padding-top:20rem;

        .arrow1{
            position: absolute;
            left: 20%;
            top: 8rem;
        }

        .arrow3{
            position: absolute;
            left: 48%;
            top:-5rem;
        }

        .arrow2{
            position: absolute;
            right: 27rem;
            right: 20%;
            top:8rem;
        }

        .outt{
            display: flex;
            justify-content: center;

        .got{
            
            text-align: center;  
            position: relative;
    display: inline-block;
    width: 100%;
    max-width: 463px;
    min-height: 235px;
    margin: 0 auto; 
  
            
        }



        .got:hover .im,
        .got:hover .h1,
        .got:hover .h5
         {
            cursor: pointer;

          }



        .cont{
            position: relative;
        }

        .h5{
            position: absolute; 
            top: 5rem;
            left: 0;
            right: 0;
            font-family: Source Code Pro,monospace;
            font-weight: 500;
            line-height: 24px;
            color: #12110c;
            font-size: 1.6rem;
        
        }

        .h1{
            position: absolute;
            top: 10rem;
            left: 0;
            right:0;
                    
            color: #12110c;
            font-size: 6.4rem;
            font-weight: 800;
            line-height: 56px;
            font-family: Metropolis,sans-serif;
        strong{
            color: #f6bbd4;
            font: inherit;
        }
        }
    }
        .guy{
            display: block;
            margin: 2rem auto;
            // filter: brightness(30%) saturate(10%) invert(63%) sepia(82%) saturate(322%) hue-rotate(352deg) brightness(96%) contrast(86%);

        }
    
    }

}



@media screen and (max-width: 470px) {
    .im{
        width:37rem!important;
        transform: translateY(25px)!important;
    }

}

@media screen and (max-width: 378px) {

    .got{
        transform: scale(0.8)!important;
    }

}