.resume-section{

    margin-top: -1px;
    // padding-bottom: 4rem;
    // overflow: hidden;

    background-color:#07251f;
    padding-bottom: 10rem;



    .resume{
      clip-path: polygon(0 0,100% 0,100% 100%,0 100%);
      max-width: 130rem;
      margin: 0 auto;


      position: relative;
      
      // height: 100rem;
      padding: 3rem;
      background: rgb(230,230,230);
      background-color: #07251f;
      
      color: rgb(50,50,50);
      font-family: 'Open Sans', sans-serif;
      // font-size: 112.5%;
      font-size: 152.5%;
      line-height: 1.6em;


      .jj{
        max-width: 10rem;
        filter: grayscale(1);
        position: absolute;
        z-index: 1;
        right: 15rem;
        top:12rem;
        transform: translateY(0);
        animation: float5 5s ease-in-out infinite;
    }

    @keyframes float5 {
        0% {
            transform: translatey(0);
        }
        
        50% {
            transform: translatey(2rem);
        }
        100% {
            transform: translatey(0);
        }
        
    }
      

      .h52{
        margin-left: 7rem;
        font-family: 'Source Code Pro', monospace;
        font-weight: 500;
        line-height: 2.4rem;
        font-size: 1.6rem;
        padding-top: 3rem;
        color: #e4e4e4;
    }

    .lh22{
        margin: 2.4rem 0 7rem 7rem;
        font-size: 4rem;
        font-weight: 800;
        line-height: 5.6rem;
        padding: 0;
        border: 0;
        display: inline-block;
        position: relative;

        vertical-align: baseline;
        font-family: 'Poppins', sans-serif;
        color:#e4a654;
        strong{
            color: #e4e4e4;
        }

        
        .im{
            position: relative;

        }
        .cut1{
            position: absolute;
            
            bottom: -0.4rem;
            right: 0.8rem;
            
        }

        .house-icon{
            filter: brightness(30%) saturate(10%) invert(63%) sepia(82%) saturate(322%) hue-rotate(352deg) brightness(96%) contrast(86%);
            position: absolute;
            top: -2.5rem;
            right: -7rem;
            
        }
    }
        


        .desc{
            color: #ececec;
        }


          
          /* ================ The Timeline ================ */
          
          .timeline {
            position: relative;
            width: 66rem;
            margin: 0 auto;
            margin-top: 2rem;
            padding: 1em 0;
            list-style-type: none;
            
        
          }
          
          .timeline:before {
            position: absolute;
            left: 50%;
            top: 0;
            content: ' ';
            display: block;
            width: 0.6rem;
            height: 100%;
            margin-left: -3px;
            background: rgb(80,80,80);
            background: linear-gradient(to bottom, rgba(80,80,80,0) 0%, rgb(80,80,80) 8%, rgb(80,80,80) 92%, rgba(80,80,80,0) 100%);
            
            z-index: 5;
          }
          
          .timeline li {
            padding: 1em 0;
          }
          
          .timeline li:after {
            content: "";
            display: block;
            height: 0;
            clear: both;
            visibility: hidden;
          }
          
          .direction-l {
            position: relative;
            width: 30rem;
            float: left;
            text-align: right;
          }
          
          .direction-r {
            position: relative;
            width: 30rem;
            float: right;
          }
          
          .flag-wrapper {
            position: relative;
            display: inline-block;
            
            text-align: center;
          }
          
          .flag {
            position: relative;
            display: inline;
            background: #e4e4e4;
            padding: 0.6rem 1rem;
            border-radius: 0.5rem;
            
            font-weight: 600;
            text-align: left;
          }
          
          .direction-l .flag {
            box-shadow: -1px 1px 1px rgba(0,0,0,0.15), 0 0 1px rgba(0,0,0,0.15);
          }
          
          .direction-r .flag {
            box-shadow: 1px 1px 1px rgba(0,0,0,0.15), 0 0 1px rgba(0,0,0,0.15);
          }
          
          .direction-l .flag:before,
          .direction-r .flag:before {
            position: absolute;
            top: 50%;
            right: -4rem;
            content: ' ';
            display: block;
            width: 1.2rem;
            height: 1.2rem;
            margin-top: -1rem;
            background: #e4e4e4;
            border-radius: 1rem;
            border: 4px solid rgb(255,80,80);
            z-index: 10;
            transition: all 0.3s;
          }
          
          .direction-r .flag:before {
            left: -4rem;
          }
          
          .direction-l .flag:after {
            content: "";
            position: absolute;
            left: 100%;
            top: 50%;
            height: 0;
            width: 0;
            margin-top: -8px;
            border: solid transparent;
            border-left-color: #e4e4e4;
            border-width: 8px;
            pointer-events: none;
          }
          
          .direction-r .flag:after {
            content: "";
            position: absolute;
            right: 100%;
            top: 50%;
            height: 0;
            width: 0;
            margin-top: -8px;
            border: solid transparent;
            border-right-color: #e4e4e4;
            border-width: 8px;
            pointer-events: none;
          }
          
          .time-wrapper {
            display: inline;
            
            line-height: 1em;
            font-size: 0.66666em;
            color: rgb(250,80,80);
            vertical-align: middle;
          }


          
          .direction-l .time-wrapper {
            float: left;
          }
          
          .direction-r .time-wrapper {
            float: right;
          }
          
          .time {
            display: inline-block;
            padding: 0.4rem 0.6rem;
            background: #e4e4e4;
          }
          
          .desc {
            margin: 1em 0.75em 0 0;
            
            font-size: 0.77777em;
            font-style: italic;
            line-height: 1.5em;
          }
          
          .direction-r .desc {
            margin: 1em 0 0 0.75em;
          }

          .direction-r:hover .flag:before,
          .direction-l:hover .flag:before{
            
            border: 4px solid rgba(255, 255, 255, 0.26);
          }
          
          /* ================ Timeline Media Queries ================ */
          
          @media screen and (max-width: 660px) {
          
          .timeline {
               width: 100%;
              padding: 4em 0 1em 0;
          }
          
          .timeline li {
              padding: 2em 0;
          }
          
          .direction-l,
          .direction-r {
              float: none;
              width: 100%;
          
              text-align: center;
          }
          
          .flag-wrapper {
              text-align: center;
          }
          
          .flag {
              background: #e4e4e4;
              z-index: 15;
          }
          
          .direction-l .flag:before,
          .direction-r .flag:before {
            position: absolute;
            top: -30px;
              left: 50%;
              content: ' ';
              display: block;
              width: 12px;
              height: 12px;
              margin-left: -9px;
              background: #e4e4e4;
              border-radius: 10px;
              border: 4px solid rgb(255,80,80);
              z-index: 10;
          }
          
          .direction-l .flag:after,
          .direction-r .flag:after {
              content: "";
              position: absolute;
              left: 50%;
              top: -8px;
              height: 0;
              width: 0;
              margin-left: -8px;
              border: solid transparent;
              border-bottom-color: #e4e4e4;
              border-width: 8px;
              pointer-events: none;
          }
          
          .time-wrapper {
              display: block;
              position: relative;
              margin: 4px 0 0 0;
              z-index: 14;
          }
          
          .direction-l .time-wrapper {
              float: none;
          }
          
          .direction-r .time-wrapper {
              float: none;
          }
          
          .desc {
              position: relative;
              margin: 1em 0 0 0;
              padding: 1em;
              background: rgb(245,245,245);
              box-shadow: 0 0 1px rgba(0,0,0,0.20);
              
            z-index: 15;
          }
          
          .direction-l .desc,
          .direction-r .desc {
            color: black;
              position: relative;
              margin: 1em 1em 0 1em;
              padding: 1em;
              
            z-index: 15;
          }
          
          }
          
        //   @media screen and (min-width: 400px) and (max-width: 660px) {
          
        //   .direction-l .desc,
        //   .direction-r .desc {
        //       margin: 1em 4em 0 4em;
        //   }
          
        //   }

    
    }
}

