          clip-path: polygon(0 4%, 100% 0%, 100% 99%, 0% 95%);
  padding-top: 10.5rem;
  padding-bottom: 15rem;
  max-width: 130rem;
  margin: 0 auto;
}
.cover .pursuits-section .pursuits {
  position: relative;
  max-width: 130rem;
  margin: 0 auto;
}
.cover .pursuits-section .pursuits .container {
  padding-top: 3rem;
  margin: 0 auto;
  max-width: 130rem;
}
.cover .pursuits-section .pursuits .container .h5 {
  margin-left: 10rem;
  font-family: "Source Code Pro", monospace;
  font-weight: 500;
  line-height: 2.4rem;
  font-size: 1.6rem;
  padding-top: 2rem;
  color: rgb(255, 255, 255);
}
.cover .pursuits-section .pursuits .container .lh2 {
  margin-left: 10rem;
  margin: 2.4rem 0 0 10rem;
  font-size: 4rem;
  font-weight: 800;
  line-height: 5.6rem;
  padding: 0;
  border: 0;
  margin-bottom: 4rem;
  vertical-align: baseline;
  font-family: "Poppins", sans-serif;
  color: #e4a654;
}
.cover .pursuits-section .pursuits .container .lh2 strong {
  color: rgb(255, 255, 255);
}
.cover .pursuits-section .pursuits .container .services {
  margin-top: 5rem;
  padding-bottom: 7rem;
  max-width: 126.32rem;
  background-color: #e4e4e4;
  -webkit-clip-path: polygon(14% 0, 100% 7%, 100% 100%, 0 100%);
          clip-path: polygon(14% 0, 100% 7%, 100% 100%, 0 100%);
  margin: 0 auto;
  position: relative;
}
.cover .pursuits-section .pursuits .container .services .down {
  max-width: 130rem;
  margin: 12rem 0 0rem 6rem;
  background-color: transparent;
  padding: 3rem 7.5rem 3rem 3.5rem;
}
.cover .pursuits-section .pursuits .container .services .down .down-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: space-between;
  gap: 6rem;
  width: 100rem;
  margin: 0 auto;
}
.cover .pursuits-section .pursuits .container .services .down .down-container .number {
  display: inline-block;
  color: #e4a654;
  font-size: 1.6rem;
