  font-family: "Barlow", sans-serif;
  margin-bottom: 0.5rem;
}
.cover .pursuits-section .pursuits .container .services .down .down-container .title {
  padding-top: 1rem;
  font-size: 2rem;
  font-weight: 600;
  line-height: 1.3;
  letter-spacing: 1px;
  font-family: "Barlow", sans-serif;
  color: #000;
}
.cover .pursuits-section .pursuits .container .services .down .down-container .line {
  margin-top: 3rem;
  margin-bottom: 3rem;
  height: 0.2rem;
  background-color: rgb(202, 199, 199);
}
.cover .pursuits-section .pursuits .container .services .down .down-container .dedication:hover .line,
.cover .pursuits-section .pursuits .container .services .down .down-container .hardwork:hover .line,
.cover .pursuits-section .pursuits .container .services .down .down-container .intelligence:hover .line {
  animation: ani 0.2s ease-in forwards;
}
@keyframes ani {
  from {
    background-color: rgb(202, 199, 199);
  }
  to {
    background-color: #e4a654;
    background-color: #525151;
  }
}
.cover .pursuits-section .pursuits .container .services .down .down-container .para {
  font-family: "Poppins", sans-serif;
  font-size: 1.5rem;
  line-height: 1.6;
  letter-spacing: 0;
  word-wrap: break-word;
  font-weight: 400;
  color: #54545f;
}
.cover .pursuits-section .pursuits .container .services .tiny {
  position: absolute;
  right: 1rem;
  top: 10rem;
  filter: brightness(30%);
}
.cover .pursuits-section .pursuits .container .services .skull {
  position: absolute;
  right: 19rem;
  top: 15rem;
  animation: skull 3s infinite ease-in;
  filter: brightness(30%) saturate(10%) invert(63%) sepia(82%) saturate(322%) hue-rotate(352deg) brightness(96%) contrast(86%);
}
@keyframes skull {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(5px);
  }
  100% {
    transform: translateY(0px);
  }
}
.cover .pursuits-section .pursuits .container .services .flag-icon {
  position: absolute;
  bottom: -6rem;
  left: 14rem;
  filter: brightness(30%);
  animation: flag 4s infinite ease-in;
}
@keyframes flag {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(20px);
  }
  100% {
    transform: translateY(0px);
  }
}
.cover .pursuits-section .pursuits .container .services a {
  cursor: pointer;
}
.cover .pursuits-section .pursuits .container .services .left-arrow {
  position: absolute;
  font-size: 3.5rem;
  color: #000000;
  top: 34rem;
  left: 15%;
}
.cover .pursuits-section .pursuits .container .services .right-arrow {
  position: absolute;
  font-size: 3.5rem;
  color: #000000;
  top: 34rem;
  right: 5%;
}
.cover .pursuits-section .pursuits .container .services .out-content {
  margin: 0 auto;
  padding: 14rem 10rem 0 15rem;
  max-width: 130rem;
  height: 50rem;
}
.cover .pursuits-section .pursuits .container .services .out-content .content {
  max-width: 66rem;
  margin: 0 auto;
  padding: 2rem;
  animation: dum 0.5s ease forwards;
}
@keyframes dum {
  from {
    opacity: 0;
    transform: translateX(-8rem);
  }
  to {
    opacity: 1;
    transform: translateX(2rem);
  }
}
.cover .pursuits-section .pursuits .container .services .out-content .content .h2 {
  font-family: "Poppins", sans-serif;
  text-transform: uppercase;
  font-size: 3.6rem;
  padding-bottom: 1rem;
  color: #cc9c4a;
}
.cover .pursuits-section .pursuits .container .services .out-content .content p {
  padding-top: 3rem;
