  padding-top: 2rem;
  font-size: 1.2rem;
  letter-spacing: 0.3px;
}
.cover .pursuits-section .pursuits .r90 {
  position: absolute;
  bottom: 15.5rem;
  right: 3rem;
  width: 2.4rem;
  white-space: nowrap;
  text-transform: uppercase;
  font-size: 1.2rem;
  letter-spacing: 0.8px;
  font-family: Metropolis, sans-serif;
  font-weight: 500;
  transform: rotate(-270deg);
}
.cover .pursuits-section .pursuits a {
  display: inline-block;
  text-decoration: none;
  color: #ffffff;
  outline: none;
  color: #e4a654;
  transition: all 0.3s;
}
.cover .pursuits-section .pursuits a:hover {
  letter-spacing: 1.5px;
}
.cover .pursuits-section .pursuits a .left {
  display: inline-block;
  font-size: medium;
  transition: 0.4s;
}
.cover .pursuits-section .pursuits a:hover .left {
  transform: translateX(7px);
  color: #000000;
}

