.out{
    max-width: 130rem;
    margin: 0 auto;

}

.section-home{
    padding: 1.5rem 1.5rem 13rem 1.5rem;
    // height: 68rem;
    


    // padding: auto auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap:1rem;
    justify-content: space-between;
    // font-family: 'Source Code Pro', monospace;
    font-family: "Jost",sans-serif;
    position: relative;
    overflow: hidden;
    // background-color: #07251f;
   


    
    


    .home-content{
        position: relative;
        padding-left: 6rem;
    }
    .jj{
        max-width: 10rem;
        filter: grayscale(1);
        position: absolute;
        z-index: -1;
        left: -5rem;
        top:-5rem;
        transform: translateY(0);
        animation: float5 5s ease-in-out infinite;
    }

    @keyframes float5 {
        0% {
            transform: translatey(0);
        }
        
        50% {
            transform: translatey(1rem);
        }
        100% {
            transform: translatey(0);
        }
        
    }

    .hi-there{
        font-size: 1.4rem;
        font-weight: 600;
        letter-spacing: 1.5px;
        color: white;
        padding: 4rem 0 2rem 0;

        span{
            color: #e6bb84;
        }

    }

    .name{
        font-size: 7rem;
        color:white;
        padding-bottom:2rem ;

        span{
            color:#e6bb84;
        }
    }

    .change{
        font-size: 1.4rem;
        font-weight: 600;
        letter-spacing: 1px;
        color: white;
        padding-bottom:4rem ;

    }

    .check{

        display: flex;
        gap: 1.4rem;
        font-size: 3.2rem;
        font-weight: 600;
        letter-spacing: 1px;
        color: #e6bb84 ;
        padding-bottom:4rem ;
        font-family: 'Pangolin', cursive;
    }
    

    .about-me{
        font-size: 1.7rem;
        font-weight: 400;
        letter-spacing: 0.5px;
        line-height: 1.7;
        color: white;
        padding-bottom:3rem ;

    }

    .profiles{
        display: flex;
        gap:2rem;
        list-style: none;
        padding-bottom:4rem ;

        .social-link:link,
        .social-link:visited{
            text-decoration: none;
            color: aliceblue;
            font-size: 2.7rem;
            transition: all 0.3s;
        }

        .social-link:hover,
        .social-link:active{
            color:#e6bb84;

        }

    }
    .home__btns {
        display: flex;
        column-gap: 7rem;
    


        .btn:link,
        .btn:visited {
            border: 2px solid hsl(210,2%,65%);
            padding: 1.6rem 4rem;
            border-radius: 5.6rem;
            box-shadow: inset 0px 0px 0px 2px hsl(0, 0%, 7%),5px 4px rgb(255 255 255 / 10%);
            color: #ffffff;
            font-size: 1.3rem;
            font-weight: 700;
            display: grid;
            place-items: center;
            transition: all .4s cubic-bezier(.3, 0, .3, 1);

 
        }

        .btn:hover,
        .btn:active  {
            
            background-color: rgb(230,187,132);

        }

        .hero__link {
            color: #ffffff;
            font-size: 1.3rem;
            font-weight: 700;
            height: 5.6rem;
            display: grid;
            place-items: center;
            position: relative;
 
        }


        .hero__link:before {
            content: "";
            position: absolute;
            top: 50%;
            left: -97%;
            transform: translateY(-50%);
            width: 4rem;
            height: 2px;
            background-color: hsl(210,2%,65%);
        }

        .text-cs {
            text-transform: uppercase;
            letter-spacing: .05em;
        }

        a {
            text-decoration: none;
        }

    }

.just{
    position: relative;
}
    .home-image{
        padding-top: 1rem;
        width: 60.3rem;
        position: fixed;
        // right: 2rem;
        z-index: -99999;
        


       
        .banner{
            position: relative;
            background-color: #e6bb84; 
            // margin-top: 2rem;
            padding-top: 100%;
            border-radius: 50%;
            // z-index: 2;
            
            .img-dp{
                position: absolute; 
                border-radius: 22.8rem;
                top: -9.9rem;
                left: 4.9rem;
                width: 84%;
                filter:contrast(140%) brightness(80%) ;
            }
        }
        

        .home__data {
            border: 2px solid hsl(210,2%,65%);
            background: hsl(168, 20%, 14%);
            box-shadow: 5px 5px rgb(255 255 255 / 10%);
            padding-inline: 2rem;
            width: 25rem;
            height: 8.2rem;
            border-radius: 8.2rem;
            display: flex;
            align-items: center;
            position: absolute;
            z-index: 1;
        }

        .home__data-one {
            bottom: 8rem;
            left: -1rem;
            transform: translatey(0);
            animation: float 6s ease-in-out infinite;
        }

        p {
            line-height: 1.7;
        }

        .home__data .text-lg {
            text-align: center;
            font-size: 3.7rem;
        }

        .home__data .text-lg, .home__data .text-sm {
            width: 50%;
            color: #ffffff;
            font-weight: 700;
        }

        .home__data .text-lg b {
            position: relative;
            top: -0.4rem;
        }

        .home__data .text-lg b, .home__data .text-sm span {
            color: rgb(230,187,132);
        }

        b {
            font-weight: bold;
        }

        .home__data .text-sm {
            font-size: 1.3rem;
            line-height: 1.5em;
        }

        .text-cs {
            text-transform: uppercase;
            letter-spacing: .05em;
        }

        .home__data-two {
            bottom: 5rem;
            right: 4rem;
            transform: translatey(0);
            animation: float2 5s ease-in-out infinite;
        }

        @keyframes float {
            0% {
                transform: translatey(0);
            }
            50% {
                transform: translatey(-1.5rem);
            }
            100% {
                transform: translatey(0);
            }
            
        }

        @keyframes float2 {
            0% {
                transform: translatey(0);
            }
            
            50% {
                transform: translatey(1rem);
            }
            100% {
                transform: translatey(0);
            }
            
        }


        
        .shape {
            
            position: absolute;
            filter: invert(1);
            opacity: .6;
            z-index: -1;
        }
        
        .img-dot1{
            width: 22rem;
            height: 22rem;
            top: -6.7rem;
            right: 10.3rem;
        }

        .img-dot2{
            width: 12.6rem;
            height: 12.6rem;
            bottom: 1.8rem;
            left: -2.3rem;
        }

        .img-dot3{
            width: 12.6rem;
            height: 12.6rem;
            bottom: 1.8rem;
            left: 48.6rem;
        
        }

    }

    .deco__left {
        left: 2.3%;
    }

    .section__deco {
        position: absolute;
        top: 24rem;
        transform: translateY(-50%);
        width: 1px;
        height: 42rem;
        background-color: hsl(210,2%,65%);
        z-index: 1;
        transition: all .8s cubic-bezier(.3, 0, .3, 1);
    }

    .section__deco:before {
        top: 0;
        transform: translate(-50%,-100%);
    }

    .section__deco:before, .section__deco:after {
        content: "";
        position: absolute;
        left: 50%;
        width: 3rem;
        height: 3rem;
        border-radius: 50%;
        background-color: hsl(168, 65%, 10%);
        border: 2px solid hsl(210,2%,65%);
        box-shadow: 5px 5px rgb(255 255 255 / 10%);
    }

    .section__deco:after {
        bottom: 0;
        transform: translate(-50%,100%);
    }
    .flag-icon{
        // transform: translate3d(0px, -110px, 0px);
        position:absolute;
        left:50rem;
        bottom: -10rem;
        transform: rotate(-20deg);
        animation: flag 8s infinite ease-in;
        
    }
    
    @keyframes flag {
        0%{transform: translateY(0px) rotate(-20deg)}
        50%{transform: translateY(20px) rotate(-20deg)}
        100%{transform: translateY(0px) rotate(-20deg)}
        
    }

    .r90 {
        // padding-top: 0px;
        position: absolute;
        bottom: 6.5rem;
        left:0.6rem;
        width: 2.4rem;
        white-space: nowrap;
        text-transform: uppercase;
        font-size: 1.2rem;
        letter-spacing: 0.8px;
        font-family: Metropolis,sans-serif;
        font-weight: 500;
        transform: rotate(-90deg);
    }



    a {
        display: inline-block;
        text-decoration: none;
        color: #ffffff;
        outline: none;
        color: #e6bb84 ;
        transition: all 0.3s;
        
    }

    a:hover{
        letter-spacing: 1px;
    }

    a .left {
        display: inline-block;
        font-size: medium;
        transition: 0.4s; 
        
      }

    a:hover .left {
        transform: translateX(-0.7rem);
        color: #ffffff;

      }


}


@media screen and (max-width:1200px) {
    .section-home{
        display:block!important;
        
    }

    .deco__left{
        display: none!important;

    }

    .check{
        margin-bottom: 52rem!important;
        display: flex!important;
        justify-content: center!important;
        // margin-left: 50%!important;
        // margin-right: auto!important;
        // margin-bottom: 19rem!important;
    }

.home-content{
    text-align: center!important;
    padding-left: 0 !important;
}

.profiles,.home__btns{
    display: flex!important;
    justify-content: center!important;
}


    .just{
        position: relative!important;
        .home-image{
            position: absolute !important;
            top: -48rem;
            left: 50%;
            transform: translate(-50%, -50%) scale(0.83) !important;
            
        
        }
    }

    .move{
        transform: translateY(9rem);
    }

    .flag-icon{
        left: 8rem !important;;
        bottom: -15rem !important;
    }
    
}


