.contact-section {
  background-color: #ffffff;
  padding-bottom: 3rem;
}
.contact-section .contact {
  max-width: 130rem;
  margin: 0 auto;
  position: relative;
  padding-top: 20rem;
}
.contact-section .contact .arrow1 {
  position: absolute;
  left: 20%;
  top: 8rem;
}
.contact-section .contact .arrow3 {
  position: absolute;
  left: 48%;
  top: -5rem;
}
.contact-section .contact .arrow2 {
  position: absolute;
  right: 27rem;
  right: 20%;
  top: 8rem;
}
.contact-section .contact .outt {
  display: flex;
  justify-content: center;
}
.contact-section .contact .outt .got {
  text-align: center;
  position: relative;
  display: inline-block;
  width: 100%;
  max-width: 463px;
  min-height: 235px;
  margin: 0 auto;
}
.contact-section .contact .outt .got:hover .im,
.contact-section .contact .outt .got:hover .h1,
.contact-section .contact .outt .got:hover .h5 {
  cursor: pointer;
}
.contact-section .contact .outt .cont {
  position: relative;
}
.contact-section .contact .outt .h5 {
  position: absolute;
  top: 5rem;
  left: 0;
  right: 0;
  font-family: Source Code Pro, monospace;
  font-weight: 500;
  line-height: 24px;
  color: #12110c;
  font-size: 1.6rem;
}
.contact-section .contact .outt .h1 {
  position: absolute;
  top: 10rem;
  left: 0;
  right: 0;
  color: #12110c;
  font-size: 6.4rem;
  font-weight: 800;
  line-height: 56px;
  font-family: Metropolis, sans-serif;
}
.contact-section .contact .outt .h1 strong {
  color: #f6bbd4;
  font: inherit;
}
.contact-section .contact .guy {
  display: block;
  margin: 2rem auto;
}

@media screen and (max-width: 470px) {
  .im {
    width: 37rem !important;
    transform: translateY(25px) !important;
  }
}
@media screen and (max-width: 378px) {
  .got {
    transform: scale(0.8) !important;
  }
}/*# sourceMappingURL=Contact.css.map */