.out {
  max-width: 130rem;
  margin: 0 auto;
}

.section-home {
  padding: 1.5rem 1.5rem 13rem 1.5rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  justify-content: space-between;
  font-family: "Jost", sans-serif;
  position: relative;
  overflow: hidden;
}
.section-home .home-content {
  position: relative;
  padding-left: 6rem;
}
.section-home .jj {
  max-width: 10rem;
  filter: grayscale(1);
  position: absolute;
  z-index: -1;
  left: -5rem;
  top: -5rem;
  transform: translateY(0);
  animation: float5 5s ease-in-out infinite;
}
@keyframes float5 {
  0% {
    transform: translatey(0);
  }
  50% {
    transform: translatey(1rem);
  }
  100% {
    transform: translatey(0);
  }
}
.section-home .hi-there {
  font-size: 1.4rem;
  font-weight: 600;
  letter-spacing: 1.5px;
  color: white;
  padding: 4rem 0 2rem 0;
}
.section-home .hi-there span {
  color: #e6bb84;
}
.section-home .name {
  font-size: 7rem;
  color: white;
  padding-bottom: 2rem;
}
.section-home .name span {
  color: #e6bb84;
}
.section-home .change {
  font-size: 1.4rem;
  font-weight: 600;
  letter-spacing: 1px;
  color: white;
  padding-bottom: 4rem;
}
.section-home .check {
  display: flex;
  gap: 1.4rem;
  font-size: 3.2rem;
  font-weight: 600;
  letter-spacing: 1px;
  color: #e6bb84;
  padding-bottom: 4rem;
  font-family: "Pangolin", cursive;
}
.section-home .about-me {
  font-size: 1.7rem;
  font-weight: 400;
  letter-spacing: 0.5px;
  line-height: 1.7;
  color: white;
  padding-bottom: 3rem;
}
.section-home .profiles {
  display: flex;
  gap: 2rem;
  list-style: none;
  padding-bottom: 4rem;
}
.section-home .profiles .social-link:link,
.section-home .profiles .social-link:visited {
  text-decoration: none;
  color: aliceblue;
  font-size: 2.7rem;
  transition: all 0.3s;
}
.section-home .profiles .social-link:hover,
.section-home .profiles .social-link:active {
  color: #e6bb84;
}
.section-home .home__btns {
  display: flex;
  -moz-column-gap: 7rem;
       column-gap: 7rem;
}
.section-home .home__btns .btn:link,
.section-home .home__btns .btn:visited {
  border: 2px solid hsl(210, 2%, 65%);
  padding: 1.6rem 4rem;
  border-radius: 5.6rem;
  box-shadow: inset 0px 0px 0px 2px hsl(0, 0%, 7%), 5px 4px rgba(255, 255, 255, 0.1);
  color: #ffffff;
  font-size: 1.3rem;
  font-weight: 700;
  display: grid;
  place-items: center;
  transition: all 0.4s cubic-bezier(0.3, 0, 0.3, 1);
}
.section-home .home__btns .btn:hover,
.section-home .home__btns .btn:active {
  background-color: rgb(230, 187, 132);
}
.section-home .home__btns .hero__link {
  color: #ffffff;
  font-size: 1.3rem;
  font-weight: 700;
  height: 5.6rem;
  display: grid;
  place-items: center;
  position: relative;
}
.section-home .home__btns .hero__link:before {
  content: "";
  position: absolute;
  top: 50%;
  left: -97%;
  transform: translateY(-50%);
  width: 4rem;
  height: 2px;
  background-color: hsl(210, 2%, 65%);
}
.section-home .home__btns .text-cs {
  text-transform: uppercase;
  letter-spacing: 0.05em;
}
.section-home .home__btns a {
  text-decoration: none;
}
.section-home .just {
  position: relative;
}
.section-home .home-image {
  padding-top: 1rem;
  width: 60.3rem;
  position: fixed;
  z-index: -99999;
}
.section-home .home-image .banner {
  position: relative;
  background-color: #e6bb84;
  padding-top: 100%;
  border-radius: 50%;
}
.section-home .home-image .banner .img-dp {
  position: absolute;
  border-radius: 22.8rem;
  top: -9.9rem;
  left: 4.9rem;
  width: 84%;
  filter: contrast(140%) brightness(80%);
}
.section-home .home-image .home__data {
  border: 2px solid hsl(210, 2%, 65%);
  background: hsl(168, 20%, 14%);
  box-shadow: 5px 5px rgba(255, 255, 255, 0.1);
  padding-inline: 2rem;
  width: 25rem;
  height: 8.2rem;
  border-radius: 8.2rem;
  display: flex;
  align-items: center;
  position: absolute;
  z-index: 1;
}
.section-home .home-image .home__data-one {
  bottom: 8rem;
  left: -1rem;
  transform: translatey(0);
  animation: float 6s ease-in-out infinite;
}
.section-home .home-image p {
  line-height: 1.7;
}
.section-home .home-image .home__data .text-lg {
  text-align: center;
  font-size: 3.7rem;
}
.section-home .home-image .home__data .text-lg, .section-home .home-image .home__data .text-sm {
  width: 50%;
  color: #ffffff;
  font-weight: 700;
}
.section-home .home-image .home__data .text-lg b {
  position: relative;
  top: -0.4rem;
}
.section-home .home-image .home__data .text-lg b, .section-home .home-image .home__data .text-sm span {
  color: rgb(230, 187, 132);
}
.section-home .home-image b {
  font-weight: bold;
}
.section-home .home-image .home__data .text-sm {
  font-size: 1.3rem;
  line-height: 1.5em;
}
.section-home .home-image .text-cs {
  text-transform: uppercase;
  letter-spacing: 0.05em;
}
.section-home .home-image .home__data-two {
  bottom: 5rem;
  right: 4rem;
  transform: translatey(0);
  animation: float2 5s ease-in-out infinite;
}
@keyframes float {
  0% {
    transform: translatey(0);
  }
  50% {
    transform: translatey(-1.5rem);
  }
  100% {
    transform: translatey(0);
  }
}
@keyframes float2 {
  0% {
    transform: translatey(0);
  }
  50% {
    transform: translatey(1rem);
  }
  100% {
    transform: translatey(0);
  }
}
.section-home .home-image .shape {
  position: absolute;
  filter: invert(1);
  opacity: 0.6;
  z-index: -1;
}
.section-home .home-image .img-dot1 {
  width: 22rem;
  height: 22rem;
  top: -6.7rem;
  right: 10.3rem;
}
.section-home .home-image .img-dot2 {
  width: 12.6rem;
  height: 12.6rem;
  bottom: 1.8rem;
  left: -2.3rem;
}
.section-home .home-image .img-dot3 {
  width: 12.6rem;
  height: 12.6rem;
  bottom: 1.8rem;
  left: 48.6rem;
}
.section-home .deco__left {
  left: 2.3%;
}
.section-home .section__deco {
  position: absolute;
  top: 24rem;
  transform: translateY(-50%);
  width: 1px;
  height: 42rem;
  background-color: hsl(210, 2%, 65%);
  z-index: 1;
  transition: all 0.8s cubic-bezier(0.3, 0, 0.3, 1);
}
.section-home .section__deco:before {
  top: 0;
  transform: translate(-50%, -100%);
}
.section-home .section__deco:before, .section-home .section__deco:after {
  content: "";
  position: absolute;
  left: 50%;
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  background-color: hsl(168, 65%, 10%);
  border: 2px solid hsl(210, 2%, 65%);
  box-shadow: 5px 5px rgba(255, 255, 255, 0.1);
}
.section-home .section__deco:after {
  bottom: 0;
  transform: translate(-50%, 100%);
}
.section-home .flag-icon {
  position: absolute;
  left: 50rem;
  bottom: -10rem;
  transform: rotate(-20deg);
  animation: flag 8s infinite ease-in;
}
@keyframes flag {
  0% {
    transform: translateY(0px) rotate(-20deg);
  }
  50% {
    transform: translateY(20px) rotate(-20deg);
  }
  100% {
    transform: translateY(0px) rotate(-20deg);
  }
}
.section-home .r90 {
  position: absolute;
  bottom: 6.5rem;
  left: 0.6rem;
  width: 2.4rem;
  white-space: nowrap;
  text-transform: uppercase;
  font-size: 1.2rem;
  letter-spacing: 0.8px;
  font-family: Metropolis, sans-serif;
  font-weight: 500;
  transform: rotate(-90deg);
}
.section-home a {
  display: inline-block;
  text-decoration: none;
  color: #ffffff;
  outline: none;
  color: #e6bb84;
  transition: all 0.3s;
}
.section-home a:hover {
  letter-spacing: 1px;
}
.section-home a .left {
  display: inline-block;
  font-size: medium;
  transition: 0.4s;
}
.section-home a:hover .left {
  transform: translateX(-0.7rem);
  color: #ffffff;
}

