.head-container {
  display: flex;
  justify-content: space-between;
  padding: 3rem 3rem 0 2rem;
  color: white;
  max-width: 130rem;
  margin: 0 auto;
  font-size: 1.3rem;
  font-family: "Source Code Pro", monospace;
  font-weight: 600;
  letter-spacing: 3px;
}
.head-container .header-list {
  display: flex;
  gap: 2rem;
  list-style: none;
}
.head-container .header-list .main-nav-link:link,
.head-container .header-list .main-nav-link:visited {
  text-decoration: none;
  color: white;
  transition: all 0.3s;
}
.head-container .header-list .main-nav-link:active,
.head-container .header-list .main-nav-link:hover {
  color: #e6bb84;
}
.head-container .cup:link,
.head-container .cup:visited {
  display: inline-block;
  color: rgb(255, 255, 255);
  text-decoration: none;
  font-size: 3.5rem;
  transition: all 0.3s;
  color: #e6bb84;
}
.head-container .cup:active,
.head-container .cup:hover {
  color: #e6bb84;
  color: rgb(255, 255, 255);
}
.head-container .cup-pos {
  position: relative;
  z-index: 1;
}
.head-container #container {
  position: absolute;
  top: 0rem;
}
.head-container #container .steam {
  position: absolute;
  height: 15px;
  width: 15px;
  border-radius: 50%;
  background-color: #ffffff;
  z-index: -1;
  opacity: 0;
}
.head-container #container #steam1 {
  animation: steam1 2.5s ease-out infinite;
}
.head-container #container #steam2 {
  animation: steam2 2.5s ease-out 0.5s infinite;
}
.head-container #container #steam4 {
  animation: steam2 2.5s ease-out 1.5s infinite;
}
.head-container #container #steam3 {
  animation: steam1 2.5s ease-out 1s infinite;
}
@keyframes steam1 {
  0% {
    transform: translateY(0) translateX(10px) scale(0.2);
    opacity: 0.7;
  }
  100% {
    transform: translateY(-27px) translateX(-5px) scale(0.4);
    opacity: 0;
  }
}
@keyframes steam2 {
  0% {
    transform: translateY(0) translateX(6px) scale(0.2);
    opacity: 0.7;
  }
  100% {
    transform: translateY(-20px) translateX(12px) scale(0.4);
    opacity: 0;
  }
}

@media screen and (max-width: 600px) {
  .header-list {
    display: none !important;
  }
}/*# sourceMappingURL=HeaderFile.css.map */