.head-container{
    display: flex;
    justify-content: space-between;
    padding: 3rem 3rem 0 2rem;
    color: white;
    max-width: 130rem;
    margin: 0 auto;
    
    font-size: 1.3rem;
    // font-family: "Jost",sans-serif;
    font-family: 'Source Code Pro', monospace;
    font-weight: 600;
    letter-spacing: 3px;
    // position: fixed;
    // background-color: antiquewhite;




    

    .header-list{
        display: flex;
        gap: 2rem;
        list-style: none;

        .main-nav-link:link,
        .main-nav-link:visited{
            text-decoration: none;
            color: white;
            transition: all 0.3s;
        }

        .main-nav-link:active,
        .main-nav-link:hover{
            color:#e6bb84 ;
        }
    }

    .cup:link,
    .cup:visited{
        display: inline-block;
        color: rgb(255, 255, 255);
        text-decoration: none;
        font-size: 3.5rem;
        transition: all 0.3s; 
        color: #e6bb84;
        
    }

    .cup:active,
    .cup:hover{
        color: #e6bb84;
        color: rgb(255, 255, 255);
    }

    .cup-pos{
        position: relative; 
        z-index: 1;
    }


    #container {
        position: absolute;
        top: 0rem;
    
    .steam {
      position: absolute;
      height: 15px;
      width: 15px;
      border-radius: 50%;
      background-color: #ffffff;
      z-index: -1   ;
      opacity: 0;
    }
    
    #steam1 {
      animation: steam1 2.5s ease-out infinite;
    }
    
    #steam2 {
        animation: steam2 2.5s ease-out 0.5s infinite;
      }
      
    #steam4 {
        animation: steam2 2.5s ease-out 1.5s infinite;
      }

    
    #steam3 {
      animation: steam1 2.5s ease-out 1s infinite;
    }
    
    
    @keyframes steam1 {
      0% {transform: translateY(0) translateX(10px) scale(0.20); opacity: 0.7;}
      100% {transform: translateY(-27px) translateX(-5px) scale(0.40); opacity: 0;}
    }
    
    @keyframes steam2 {
      0% {transform: translateY(0) translateX(6px) scale(0.20); opacity: 0.7;}
      100% {transform: translateY(-20px) translateX(12px) scale(0.4); opacity: 0;}
    }
    }


}


@media screen and (max-width:600px) {
  .header-list{
    display: none !important;
  }
  
}