.contactpage-section{
    background-color: #ffffff;

    .contactpage{
        max-width: 130rem;
        margin: 0 auto;
        display: grid;
        grid-template-columns: 1fr 1fr;
        justify-content:center;

        .form{
            .back{
                height: 10rem;
                width: 7rem;
                background-color: #e4a654;
                // background-color: #000000;
                margin-left: 4rem;
                margin-top: 3rem;
                clip-path: polygon(0 0,100% 0,100% 60%,0 125%);
                text-decoration: none;
                cursor: pointer;

                h2{
                    color: #f2f2f2;
                    // color: #000000;
                    font-family: Metropolis,sans-serif;
                    font-size: 2.3rem;
                    font-weight: 600;
                    padding: 1.6rem 1rem 1rem 0.5rem;
                    
                }

            }

            .form-container{
                // background-color: antiquewhite;
                margin: 5rem auto 0 9rem;
                // height: 40rem;
                width: 50rem;
                

                .h5{
                    font-family: Source Code Pro,monospace;
                    font-weight: 600;
                    line-height: 2.4rem;
                    color: #12110c;
                    font-size: 1.6rem;
                    margin-bottom: 3.2rem;
                }

                .h1 {
                    color: #12110c;
                    font-size: 3rem;
                    font-weight: 800;
                    line-height: 5.6rem;
                    font-family: Metropolis,sans-serif
                }

                p {
                    color: rgba(18,17,12,.5);
                    margin-bottom: 3.2rem;
                    font-family: Metropolis,sans-serif;
                    font-size: 1.6rem;
                    font-weight: 500;
                    line-height: 3.2rem;
                }

                .theform{
                    .fullname{
                        box-shadow: none;
                        outline: none;
                        padding-left:2rem;
                        width: 100%;
                        // padding-right: 30rem;
                        background-color: #f2f2f2;
                        line-height: 8.6rem;
                        font-size: 1.4rem;
                        font-family: Metropolis,sans-serif;
                        font-weight: 500;
                        letter-spacing: 1px;
                        border: 2px solid transparent;
                        margin-bottom: 3.2rem;
                        // transition: all .35s ease infinite;
                    }

                     .fullname:focus{
                        border: 2px solid pink;
                        border: 2px solid #d8a45f80;
                    }

                    .textarea{
                        min-height: 230px;
                        line-height: 22px;
                        margin: 0;
                        padding: 40px 140px 40px 24px;
                        resize: none;
                    }

                    .button{
                        margin-left: 73%;
                        background-color: #000000;
                        color: #fdf2e9;
                        /* align-self: end; */
                        margin-top: 1.4rem;
                        padding:1.5rem 2.5rem 1.5rem 4.2rem;
                        
                        border-style: none;
                        cursor: pointer;
                        clip-path: polygon(20% 0,100% 0,100% 100%,0 100%);
                        font-size: 14px;
                        font-family: Metropolis,sans-serif;
                        letter-spacing: 1px;
                        line-height: 16px;
                        text-transform: lowercase;
                        text-align: center;
                        margin-bottom: 15rem;
                        font-weight: 700;

                    }

                    .button:hover{
                        color: #e4a654;

                    }
                }
            }

            
        }

        .map{
            clip-path: polygon(2% 2.3%,94% 0,95% 100%,12% 100%);
        }

        .frame{
            filter: grayscale(1);
        }

    }
}

@media screen and (max-width: 1140px){
    .contactpage{
        grid-template-columns: 1fr !important;
        justify-content: center !important;
        align-items: center !important;
        max-width: 70rem!important;
        margin: 0 auto;
    }


}

@media screen and (max-width: 668px){
    .contactpage{
        grid-template-columns: 1fr !important;
        justify-content: center !important;
        align-items: center !important;
        max-width: 70rem!important;
        margin: 0 auto!important;

        .back{
            margin-left: 8rem!important;

        }

        .form-container{
            max-width: 50rem!important;
            

            .h5{
                font-size: 1.8rem!important;

            }

            p {
                font-size: 1.4rem!important;

            }

            .fullname{
                width: 70%!important;
            }

            .button{
                margin-left: 43%!important;
            }
        }


    }

    .map{
        width:40rem;
        margin: 0 auto;
    }


}


@media screen and (max-width: 450px){
    .contactpage{
        grid-template-columns: 1fr !important;
        justify-content: center !important;
        align-items: center !important;
        max-width: 70rem!important;
        margin: 0 auto!important;

        .back{
            background-color: #e4a654;
            margin-left: 15rem!important;
            margin-top: 3rem;
        }

        .form-container{
            margin-left: 15rem!important;
            max-width: 50rem!important;
            

            .h5{
                font-size: 1.6rem!important;

            }

            p {
                font-size: 1.2rem!important;

            }
        }
    }
}