.footer-section{
    background-color: #07251f;
    background-color: #12110c;
    .footer{
        height: 80px;
        padding: 15px 0 0;
        max-width: 130rem;
        margin: 0 auto;

        .flex{
            display: flex;
            justify-content:space-between;
            .dummy{
                width: 18rem;
            }
            

            .h6{
                
        
                display: inline-block;
                width: 100%;
                max-width: 31rem;
                font-family: Metropolis,sans-serif;
                color: #fff;
                line-height: 52px;
                font-size: 12px;
                font-weight: 500;

                .copyright{
                    font-family: Metropolis,sans-serif;
                    font: inherit;
                }

                .separator{
                    padding: 0 5px;
                    letter-spacing: 2px;
                    font: inherit;
                }

                .acknowledgment{
                    font: inherit;
                    .blue{
                        font-size: 1.6rem !important;
                    }

                    a{
                        display: inline-block;
                        text-decoration: none;
                        outline: none;
                        color: inherit;
                        color: #f6bbd4;
                        color: #e6bb84;
                }

            }
        }

        .profiles1{
            display: flex;
            gap:2rem;
            list-style: none;
            align-items: center;
            margin-right: 2rem;
    
            .social-link1:link,
            .social-link1:visited{
                text-decoration: none;
                color: aliceblue;
                font-size: 2.1rem;
                transition: all 0.3s;
            }
    
            .social-link1:hover,
            .social-link1:active{
                color:#e6bb84;
                // color:#f6bbd4;
    
            }
    
        }
    }
}

}


@media screen and (max-width: 900px){

    .h6{
        margin-left: 2rem;
        max-width: 31rem;
        padding-bottom: 2px;
    }
    .dummy{
        display: none;
    }

}

@media screen and (max-width: 530px){

    .h6{
        // margin-left: 2rem;
        // max-width: 31rem;
        font-size: 10px!important;
    }
    .dummy{
        display: none;
    }

}

@media screen and (max-width: 500px){

    .h6{
        // margin-left: 2rem;
        // max-width: 31rem;
        font-size: 10px!important;
    }
    .dummy{
        display: none;
    }

}

@media screen and (max-width: 470px){

    .h6{
        font-size: 8px!important;
    }
    .dummy{
        display: none;
    }

}

@media screen and (max-width: 426px){

    .h6{
        font-size: 8px!important;
    }
    .dummy{
        display: none;
    }

    .profiles1{

    .social-link1:link,
    .social-link1:visited{
        font-size: 14px !important;

    }
}

}

@media screen and (max-width: 380px){


    .h6{
        font-size: 7px!important;
    }
    .dummy{
        display: none;
    }

    .profiles1{

    .social-link1:link,
    .social-link1:visited{
        font-size: 12px !important;

    }
}

}

