.cover1{
    background-color: #07251f;
    // clip-path: polygon(0 0, 100% 0%, 100% 100%, 0% 100%);
    margin-top: -1px;
    .portfolio-section{
        // padding-top: 1px;
        background-color:#e4e4e4 ;
        z-index: 99;
        // height: 100.7rem;
        padding-bottom: 9.5rem;
        // padding-top: 10.5rem;
        max-width: 130rem;
        margin: 0 auto;
        clip-path: polygon(0 0, 100% 0%, 100% 94%, 0% 100%);
    
        .portfolio{
            position: relative;
            max-width: 130rem;
            margin: 0 auto;

            .container1{
                margin: 0 auto;
                position: relative;

                max-width: 130rem;
                // justify-content: space-between;

                a{
                    cursor: pointer;
                }
                
                .left-arrow1{
                    position: absolute;
                    font-size: 3rem;
                    color: #000000;
                    top: 25rem;
                    // right:37rem;
                    right:20%;
                    z-index: 1;


                }

                .right-arrow1{
                    position: absolute;
                    font-size: 3rem;
                    color: #000000;
                    top: 25rem;
                    // right: 27rem;
                    right: 15%;
                    z-index: 1;
                }


                .tiny1{
                    position: absolute;
                    width: 15rem;
                    right: 3rem;
                    top: -13rem;
                    filter: brightness(0%) invert(0);
        
                }
        
                .skull1{
                    width: 4rem;
                    position: absolute;
                    right: 18rem;
                    top: -10rem;
                    animation: skull1 3s infinite ease-in;
                    
                    filter: brightness(30%) saturate(10%) invert(63%) sepia(82%) saturate(322%) hue-rotate(352deg) brightness(96%) contrast(86%); 
                }

                @keyframes skull1 {
                    0%{transform: translateY(0px) }
                    50%{transform: translateY(5px) }
                    100%{transform: translateY(0px) }
                    
                }
            

            .h51{
                margin-left: 10rem;
                font-family: 'Source Code Pro', monospace;
                font-weight: 500;
                line-height: 2.4rem;
                font-size: 1.6rem;
                padding-top: 3rem;
                color: rgb(0, 0, 0);
            }

            .lh21{
                margin-left: 10rem;
                margin: 2.4rem 0 0 10rem;
                font-size: 4rem;
                font-weight: 800;
                line-height: 5.6rem;
                padding: 0;
                border: 0;
                display: inline-block;
                position: relative;
        
                vertical-align: baseline;
                font-family: 'Poppins', sans-serif;
                color:#e4a654;
                strong{
                    color: rgb(0, 0, 0);
                }

                
                .im{
                    position: relative;
 
                }
                .cut1{
                    position: absolute;
                    
                    bottom: -0.4rem;
                    right: 0.8rem;
                    
                }

                .house-icon{
                    filter: brightness(30%) saturate(10%) invert(63%) sepia(82%) saturate(322%) hue-rotate(352deg) brightness(96%) contrast(86%);
                    position: absolute;
                    top: -2.5rem;
                    right: -7rem;
                    
                }
            }



            .services1{
                margin-top: 5rem;
                // height: 55rem;
                // max-width: 126.32rem; 
                background-color: #e4e4e4;
                position: relative;
                padding:5rem 5rem 0 5rem;


                .flag-icon1{
                    position: absolute;
                    bottom: -34rem;
                    left: 6rem;
                    filter: brightness(30%);
                    animation: flag1 4s infinite ease-in;
                    
                }

                @keyframes flag1 {
                    0%{transform: translateY(0px) }
                    50%{transform: translateY(20px) }
                    100%{transform: translateY(0px) }
                    
                }
                

              

                .out-content1{
                    max-width: 85rem;
                    
                    margin: 0 auto;

                    .step-progress-bar {
                        display: block;
                        width: 85rem;
                        height: 2px;
                        margin:0 auto;
                        border-radius: 1px;
                        background-color: rgba(18,17,12,.12);
                        background-image: linear-gradient(90deg,#12110c,#12110c);
                        background-repeat: no-repeat;
                        background-size: 0 100%;
                        transition: background-size .4s ease-in-out ;
                        overflow: hidden;
                    }

                    .step-progress-num {
                        max-width: 130rem;
                        margin: 0 auto;
                        padding: 1rem 0;
                        font-family: Source Code Pro,monospace;
                        font-size: 1.2rem;
                        font-weight: 500;
                        line-height: 1.5rem;
                    }





                    .content1{
                        // max-width:85rem;
                        margin: 0 auto;
                        padding: 2rem;
                        animation: dum1 0.5s ease forwards;

                        @keyframes dum1 {
                            0% {
                                opacity: 0;
                                transform: translateX(-8rem);
                            }
                            100% {
                                opacity: 1;
                                transform: translateX(-2rem);
                            }
                            
                        }


                        .image-container{
                            margin: 0 auto;
                            position: relative;
                            .image{
                                max-width: 85rem;
                                // max-width: 45rem;
                                height: 53.7698rem;
                                // border: 1px solid #e4a654;
                                
                            }


                            .image-div{
                                height: 53.8rem;
                                width: 44.5rem;
                                // background-color: #3f3731db;
                                // background-color: #3f3731e4;
                                background-color: #3f3731ec;
                                // background-color: #3f3731f3;
                                position: absolute;
                                top: 0rem;
                                left: 0rem;
                                clip-path: polygon(0% 0%,100% 0%, 80% 100%,0% 100%);
                                opacity: 0;
                                padding-right: 9rem;
                                color: #e4e4e4;
                                // display: none;
                        
                            }
                            .wrap:hover .image-div{
                                animation: change 0.3s ease-in forwards;
                                opacity: 1;
                                // display: auto;
                                
                            }

                            @keyframes change {
                                from{
                                    opacity: 0;
                                    // display: none;
                                
                                    

                                }

                                to{
                                    opacity:1;
                                    // display:auto;
                                    
                                }
                                
                            }

                            .port{
                                color:#ffffff;
                                padding-top: 15rem;
                                padding-left: 3rem;
                                font-family: 'Poppins', sans-serif;
                                font-weight: 600;
                                text-transform: uppercase;
                                font-size: 3.8rem;
                            }

                            .x{
                                color:#ffffff;
                                padding-top: 1rem;
                                padding-left: 3rem;
                                
                                font-family: Source Code Pro, monospace;
                                font-weight: 700;
                                font-size: 2.8rem;
                                line-height: 3.2rem;

                            }

                            .port-para{
                                color:#ffffff;
                                padding-top: 2rem;
                                padding-left: 3rem;
                                // font-family: "Jost",sans-serif;
                                text-transform: capitalize;
                                font-family: Source Code Pro, monospace;
                                font-weight: 600;
                                font-size: 1.8rem;
                                line-height: 3.2rem;
                                
                            }


                            .links{
                                // padding-left: 3rem;
                                padding-top: 4rem;
                                display: flex;
                                // padding-left: 9rem;
                                
                                // justify-content: space-between;

                            }

                            a{
                                margin-left: 3rem;
                                font-size: 3.2rem;
                                // color: white;
                                // color: #e4a654;
                                color: #f5b562;
                                // font-size: 1.8rem;
                                text-decoration: none;
                                transition: all 0.1s ease-in;
                                
                            }



                            a:hover{
                                // color: #e4a654;
                                color: white;
                            }
                        }

                    }


                    // button

                    .multi-button {
                        padding-top: 1.5rem;
                        display: flex;
                        justify-content:flex-start;
                        align-items: center;
                        max-width: 50rem;
                        gap: 2rem;
                        margin-bottom: 3rem;
                        // margin-left: 3rem;
                        
                        
                        
                        button {
                            clip-path: polygon(0 0,100% 0,100% 100%,0 100%);
                            cursor: pointer;
    
                          flex: 1;
    
                          position: relative;
                          padding: 1rem 2rem;
                        //   border: 2px solid #f2f2f2;
                          border: none;    
                          text-transform: uppercase;
                          font-weight: 500;
                          font-size: 1em;
                          letter-spacing: 1px;
                          box-shadow: 1px 1px 20px rgba(0,0,0,.1);
                          color: #e4a654;
                        //   background: transparent;
                          transition: all 0.09s ease-in;
                        }
    
                        button:hover,button:focus{
                            background-color: #e4a654;
                            color: #e4e4e4;
    
                        }
                      }
                    


                    
                      

                }



                
            }
        }

        .r90 {
            // padding-top: 0px;
            position: absolute;
            bottom: 0rem;
            left:0.6rem;
            width: 2.4rem;
            white-space: nowrap;
            text-transform: uppercase;
            font-size: 1.2rem;
            letter-spacing: 0.8px;
            font-family: Metropolis,sans-serif;
            font-weight: 500;
            transform: rotate(-90deg);
        }
    
    
    
        a {
            display: inline-block;
            text-decoration: none;
            color: #ffffff;
            outline: none;
            color: #e6bb84 ;
            transition: all 0.3s;
            
        }
    
        a:hover{
            letter-spacing: 1px;
        }
    
        a .left {
            display: inline-block;
            font-size: medium;
            transition: 0.4s; 
            
          }
    
        a:hover .left {
            transform: translateX(-0.7rem);
            color: #000000;
    
          }
    



        }
    }

}



