.footer-section {
  background-color: #07251f;
  background-color: #12110c;
}
.footer-section .footer {
  height: 80px;
  padding: 15px 0 0;
  max-width: 130rem;
  margin: 0 auto;
}
.footer-section .footer .flex {
  display: flex;
  justify-content: space-between;
}
.footer-section .footer .flex .dummy {
  width: 18rem;
}
.footer-section .footer .flex .h6 {
  display: inline-block;
  width: 100%;
  max-width: 31rem;
  font-family: Metropolis, sans-serif;
  color: #fff;
  line-height: 52px;
  font-size: 12px;
  font-weight: 500;
}
.footer-section .footer .flex .h6 .copyright {
  font-family: Metropolis, sans-serif;
  font: inherit;
}
.footer-section .footer .flex .h6 .separator {
  padding: 0 5px;
  letter-spacing: 2px;
  font: inherit;
}
.footer-section .footer .flex .h6 .acknowledgment {
  font: inherit;
}
.footer-section .footer .flex .h6 .acknowledgment .blue {
  font-size: 1.6rem !important;
}
.footer-section .footer .flex .h6 .acknowledgment a {
  display: inline-block;
  text-decoration: none;
  outline: none;
  color: inherit;
  color: #f6bbd4;
  color: #e6bb84;
}
.footer-section .footer .flex .profiles1 {
  display: flex;
  gap: 2rem;
  list-style: none;
  align-items: center;
  margin-right: 2rem;
}
.footer-section .footer .flex .profiles1 .social-link1:link,
.footer-section .footer .flex .profiles1 .social-link1:visited {
  text-decoration: none;
  color: aliceblue;
  font-size: 2.1rem;
  transition: all 0.3s;
}
.footer-section .footer .flex .profiles1 .social-link1:hover,
.footer-section .footer .flex .profiles1 .social-link1:active {
  color: #e6bb84;
}

@media screen and (max-width: 900px) {
  .h6 {
    margin-left: 2rem;
    max-width: 31rem;
    padding-bottom: 2px;
  }
  .dummy {
    display: none;
  }
}
@media screen and (max-width: 530px) {
  .h6 {
    font-size: 10px !important;
  }
  .dummy {
    display: none;
  }
}
@media screen and (max-width: 500px) {
  .h6 {
    font-size: 10px !important;
  }
  .dummy {
    display: none;
  }
}
@media screen and (max-width: 470px) {
  .h6 {
    font-size: 8px !important;
  }
  .dummy {
    display: none;
  }
}
@media screen and (max-width: 426px) {
  .h6 {
    font-size: 8px !important;
  }
  .dummy {
    display: none;
  }
  .profiles1 .social-link1:link,
  .profiles1 .social-link1:visited {
    font-size: 14px !important;
  }
}
@media screen and (max-width: 380px) {
  .h6 {
    font-size: 7px !important;
  }
  .dummy {
    display: none;
  }
  .profiles1 .social-link1:link,
  .profiles1 .social-link1:visited {
    font-size: 12px !important;
  }
}/*# sourceMappingURL=Footer.css.map */