.cover{
    margin-top: -1px;
    background-color: rgb(228, 228, 228);
    // border-top: 9px solid  rgb(228, 228, 228);
    // border-top: -5px;
    

    .pursuits-section{
        background-color:#07251f ;
        z-index: 1;
        // height: 144.7rem;
        clip-path: polygon(0 4%, 100% 0%, 100% 99%, 0% 95%);
        padding-top: 10.5rem;
        padding-bottom: 15rem;
        max-width: 130rem;
        margin: 0 auto;
    
        .pursuits{
            position: relative;
            max-width: 130rem;
            margin: 0 auto;

            .container{
                padding-top: 3rem;
                // display: grid;
                // grid-template-columns: 1fr 1fr;
                margin: 0 auto;

                max-width: 130rem;
                // justify-content: space-between;
            

            .h5{
                margin-left: 10rem;
                font-family: 'Source Code Pro', monospace;
                font-weight: 500;
                line-height: 2.4rem;
                font-size: 1.6rem;
                padding-top: 2rem;
                color: rgb(255, 255, 255);
            }

            .lh2{
                margin-left: 10rem;
                margin: 2.4rem 0 0 10rem;
                font-size: 4rem;
                font-weight: 800;
                line-height: 5.6rem;
                padding: 0;
                border: 0;
                margin-bottom: 4rem;
        
                vertical-align: baseline;
                font-family: 'Poppins', sans-serif;
                color:#e4a654;
                strong{
                    color: rgb(255, 255, 255);
                }
            }



            .services{
                margin-top: 5rem;
                // height: 95rem;
                padding-bottom: 7rem;
                max-width: 126.32rem;
                background-color: #e4e4e4;
                clip-path: polygon(14% 0,100% 7%,100% 100%,0 100%);
                margin: 0 auto;

                position: relative;

                .down{
                    // display: none;
                    // height:26em;
                    max-width: 130rem;
                    margin: 12rem 0 0rem 6rem;
                    background-color:transparent;
                    padding: 3rem 7.5rem 3rem 3.5rem;

                    .down-container{
                        display: grid;
                        grid-template-columns: repeat(3,1fr);
                        justify-content: space-between;
                        gap: 6rem;
                        width: 100rem;
                        margin: 0 auto;

                        .number{
                            display: inline-block;
                            color:#e4a654;
                            font-size: 1.6rem;
                            font-weight: 600;
                            font-family: "Barlow",sans-serif;
                            margin-bottom: 0.5rem;
                        }

                        .title{
                            padding-top: 1rem;
                            font-size: 2rem;
                            font-weight: 600;
                            line-height: 1.3;
                            letter-spacing: 1px;
                            font-family: "Barlow",sans-serif;
                            color: #000;
                        }

                        .line{
                            margin-top: 3rem;
                            margin-bottom: 3rem;
                            height:0.2rem;
                            // width: 32rem;
                            background-color: rgb(202, 199, 199);
                            
                        }

                        .dedication:hover .line,
                        .hardwork:hover .line,
                        .intelligence:hover .line{
                            animation: ani 0.2s ease-in forwards ;
                            
                        }

                        @keyframes ani {
                            from{
                                background-color: rgb(202, 199, 199);
                            }
                            to{
                                background-color:#e4a654;
                                background-color:#525151;
                            }
                            
                        }

                        .para{
                            font-family: 'Poppins', sans-serif;
                            font-size: 1.5rem;
                            line-height: 1.6;
                            letter-spacing: 0;
                            word-wrap: break-word;
                            font-weight: 400;
                            // background-color: #fff;
                            color: #54545f;
                        }
                    
                    }
                }




                

                .tiny{
                    position: absolute;
                    right: 1rem;
                    top: 10rem;
                    filter: brightness(30%);
        
                }
        
                .skull{
                    position: absolute;
                    right: 19rem;
                    top: 15rem;
                    animation: skull 3s infinite ease-in;
                    
                    filter: brightness(30%) saturate(10%) invert(63%) sepia(82%) saturate(322%) hue-rotate(352deg) brightness(96%) contrast(86%); 
                }

                @keyframes skull {
                    0%{transform: translateY(0px) }
                    50%{transform: translateY(5px) }
                    100%{transform: translateY(0px) }
                    
                }

                .flag-icon{
                    position: absolute;
                    bottom: -6rem;
                    left: 14rem;
                    filter: brightness(30%);
                    animation: flag 4s infinite ease-in;
                }

                @keyframes flag {
                    0%{transform: translateY(0px) }
                    50%{transform: translateY(20px) }
                    100%{transform: translateY(0px) }
                    
                }
                

                a{
                    cursor: pointer;
                }
                
                .left-arrow{
                    position: absolute;
                    font-size: 3.5rem;
                    color: #000000;
                    top: 34rem;
                    left: 15%;


                }

                .right-arrow{
                    position: absolute;
                    font-size: 3.5rem;
                    color: #000000;
                    top: 34rem;
                    right: 5%;
                }

                .out-content{
                    margin: 0 auto;
                    padding:14rem 10rem 0 15rem;
                    max-width: 130rem;
                    // background-color: #e4a654;
                    height: 50rem;

                    .content{
                        max-width:66rem;
                        // height: 50rem;
                        margin: 0 auto;
                        padding: 2rem;
                        animation: dum 0.5s ease forwards;
                        // border:  1px solid grey;

                        @keyframes dum {
                            from{
                                opacity: 0;
                                transform: translateX(-8rem);
                            }
                            to{
                                opacity: 1;
                                transform: translateX(2rem);
                            }
                            
                        }

                        // background-color: aqua;



                        .h2{
                            font-family: 'Poppins', sans-serif;
                            text-transform: uppercase;
                            font-size: 3.6rem;
                            padding-bottom: 1rem;
                            color:#cc9c4a;
                            
                        }

                        p{
                            padding-top: 3rem;
                            font-size: 1.5rem;
                            font-family: Source Code Pro,monospace;
                            line-height: 1.8;
                        }

                        .trading-list{
                            list-style-type:disc;
                            padding: 2rem 0 0 4rem;
                            font-family: Source Code Pro,monospace;
                            
                            
                            

                            li{
                                padding-top: 2rem;
                                font-size: 1.2rem;
                                letter-spacing: 0.3px;

                            }

                        }
                    }
                }



                
            }
        }


        .r90 {
            // padding-top: 0px;
            position: absolute;
            bottom: 15.5rem;
            right:3rem;
            width: 2.4rem;
            white-space: nowrap;
            text-transform: uppercase;
            font-size: 1.2rem;
            letter-spacing: 0.8px;
            font-family: Metropolis,sans-serif;
            font-weight: 500;
            transform: rotate(-270deg);
        }
    
    
    
        a {
            display: inline-block;
            text-decoration: none;
            color: #ffffff;
            outline: none;
            color: #e4a654 ;
            transition: all 0.3s;
            
        }
    
        a:hover{
            letter-spacing: 1.5px;
        }
    
        a .left {
            display: inline-block;
            font-size: medium;
            transition: 0.4s; 
            
          }
    
        a:hover .left {
            transform: translateX(7px);
            color: #000000;
    
          }




        }
    }

}


