.contactpage-section {
  background-color: #ffffff;
}
.contactpage-section .contactpage {
  max-width: 130rem;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: center;
}
.contactpage-section .contactpage .form .back {
  height: 10rem;
  width: 7rem;
  background-color: #e4a654;
  margin-left: 4rem;
  margin-top: 3rem;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 60%, 0 125%);
          clip-path: polygon(0 0, 100% 0, 100% 60%, 0 125%);
  text-decoration: none;
  cursor: pointer;
}
.contactpage-section .contactpage .form .back h2 {
  color: #f2f2f2;
  font-family: Metropolis, sans-serif;
  font-size: 2.3rem;
  font-weight: 600;
  padding: 1.6rem 1rem 1rem 0.5rem;
}
.contactpage-section .contactpage .form .form-container {
  margin: 5rem auto 0 9rem;
  width: 50rem;
}
.contactpage-section .contactpage .form .form-container .h5 {
  font-family: Source Code Pro, monospace;
  font-weight: 600;
  line-height: 2.4rem;
  color: #12110c;
  font-size: 1.6rem;
  margin-bottom: 3.2rem;
}
.contactpage-section .contactpage .form .form-container .h1 {
  color: #12110c;
  font-size: 3rem;
  font-weight: 800;
  line-height: 5.6rem;
  font-family: Metropolis, sans-serif;
}
.contactpage-section .contactpage .form .form-container p {
  color: rgba(18, 17, 12, 0.5);
  margin-bottom: 3.2rem;
  font-family: Metropolis, sans-serif;
  font-size: 1.6rem;
  font-weight: 500;
  line-height: 3.2rem;
}
.contactpage-section .contactpage .form .form-container .theform .fullname {
  box-shadow: none;
  outline: none;
  padding-left: 2rem;
  width: 100%;
  background-color: #f2f2f2;
  line-height: 8.6rem;
  font-size: 1.4rem;
  font-family: Metropolis, sans-serif;
  font-weight: 500;
  letter-spacing: 1px;
  border: 2px solid transparent;
  margin-bottom: 3.2rem;
}
.contactpage-section .contactpage .form .form-container .theform .fullname:focus {
  border: 2px solid pink;
  border: 2px solid rgba(216, 164, 95, 0.5019607843);
}
.contactpage-section .contactpage .form .form-container .theform .textarea {
  min-height: 230px;
  line-height: 22px;
  margin: 0;
  padding: 40px 140px 40px 24px;
  resize: none;
}
.contactpage-section .contactpage .form .form-container .theform .button {
  margin-left: 73%;
  background-color: #000000;
  color: #fdf2e9;
  /* align-self: end; */
  margin-top: 1.4rem;
  padding: 1.5rem 2.5rem 1.5rem 4.2rem;
  border-style: none;
  cursor: pointer;
  -webkit-clip-path: polygon(20% 0, 100% 0, 100% 100%, 0 100%);
          clip-path: polygon(20% 0, 100% 0, 100% 100%, 0 100%);
  font-size: 14px;
  font-family: Metropolis, sans-serif;
  letter-spacing: 1px;
  line-height: 16px;
  text-transform: lowercase;
  text-align: center;
  margin-bottom: 15rem;
  font-weight: 700;
}
.contactpage-section .contactpage .form .form-container .theform .button:hover {
  color: #e4a654;
}
.contactpage-section .contactpage .map {
  -webkit-clip-path: polygon(2% 2.3%, 94% 0, 95% 100%, 12% 100%);
          clip-path: polygon(2% 2.3%, 94% 0, 95% 100%, 12% 100%);
}
.contactpage-section .contactpage .frame {
  filter: grayscale(1);
}

@media screen and (max-width: 1140px) {
  .contactpage {
    grid-template-columns: 1fr !important;
    justify-content: center !important;
    align-items: center !important;
    max-width: 70rem !important;
    margin: 0 auto;
  }
}
@media screen and (max-width: 668px) {
  .contactpage {
    grid-template-columns: 1fr !important;
    justify-content: center !important;
    align-items: center !important;
    max-width: 70rem !important;
    margin: 0 auto !important;
  }
  .contactpage .back {
    margin-left: 8rem !important;
  }
  .contactpage .form-container {
    max-width: 50rem !important;
  }
  .contactpage .form-container .h5 {
    font-size: 1.8rem !important;
  }
  .contactpage .form-container p {
    font-size: 1.4rem !important;
  }
  .contactpage .form-container .fullname {
    width: 70% !important;
  }
